.header {
  height: min-content;
  max-width: 1280px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.header:after {
  content: "";
  height: 300px;
  z-index: -1;
  background-image: url("header-bg.c4f152bb.png");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

@media (width >= 768px) {
