.footer {
  padding: 80px 0 60px;

  @include onTablet {
    padding: 120px 0 36px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    font-size: 12px;
    font-weight: 300;
    line-height: 140.625%;

    @include onTablet {
      flex-direction: row-reverse;
      justify-content: space-between;
    }
  }

}