.button {
  display: flex;

  &__text {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    border: 1px solid $color-green;
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    background-image: linear-gradient(to right, #fff 50%, $color-green 50%);
    background-size: 200% 100%;
    background-position: 0 0;
    transition: all 0.5s;

    @include onTablet {
      height: 70px;
    }
  }

  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    min-width: 50px;
    min-height: 50px;
    background-color: $color-green;
    cursor: pointer;

    @include onTablet {
      min-width: 70px;
      min-height: 70px;
    }

    &--orange {
      background-color: #F14A27;
      transition: all $effectDuration;
      transform: translateX(0);
    }
  }

  &:hover .button__text {
    color: #fff;
    background-position: 100% 0;
  }
}