.contacts {
  padding-top: 50px;

  &__content {
    @include pageGrid;
  }

  &__icons {
    align-items: center;
    grid-column: 1 / -1;
    @include pageGrid;
    margin-bottom: 40px;

    @include onTablet {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 62px;
      grid-column: 1 / 1;
    }
  }

  &__social-icons {
    display: flex;
    gap: 20px;

    @include onTablet {
      gap: 32px;
    }
  }

  &__work-shedule {
    grid-column: 1 / 2;

    @include onTablet {
      grid-column: 2 / 4;
      margin-bottom: 64px;
    }

    @include onDesktop {
      grid-column: 3 / 5;
    }
  }

  &__contact {
    grid-column: 2 / -1;

    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      grid-column: 5 / 7;
    }

    &__title {
      font-size: 14px;
      font-weight: 400;
      line-height: 140.625%;
      margin-bottom: 18px;
    }

    &__link {
      display: block;
      font-size: 14px;
      font-weight: 400;
      line-height: 150%;
      transition: color $effectDuration;

      &:hover {
        color: #F14A27;
      }
    }
  }

  &__go-top {
    grid-column: 1 / -1;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;

    @include onTablet {
      display: none;
    }
  }

  &__line {
    grid-column: 1 / -1;
    margin-bottom: 40px;

    @include onTablet {
      grid-column: 2 / 6;
      margin-bottom: 64px;
    }

    @include onDesktop {
      grid-column: 7 / 8;
      margin: 0 auto;
    }
  }

  &__nav {
    @include pageGrid;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / 6;
      grid-template-columns: repeat(4, 1fr);
    }

    @include onDesktop {
      grid-column: 8 / 12;
    }

    &-list {
      grid-column: span 1;

      @include onTablet {
        grid-column: span 2;
      }
    }
  }
}