.nav {
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px 28px;

    @include onTablet {
      gap: 24px 20px;
    }

    @include onDesktop {
      gap: 30px 31px;
    }

    &--footer {
      gap: 20px;
      flex-direction: column;
      flex-wrap: nowrap;
    }
  }

  &__item {
    width: 100px;

    @include onDesktop {
      width: 168px;
    }

    &--footer {
      width: 100%;
    }
  }

  &__link {
    width: 100%;
    font-size: 16px;
    font-weight: 500;
    line-height: 100%;
    transition: color $effectDuration;

    @include onDesktop {
      font-size: 22px;
    }

    &:hover {
      color: #F14A27;
    }

    &--footer {
      position: relative;
      font-size: 14px;
      font-weight: 400;
      line-height: 100%;

      &::after {
        content: '';
        position: absolute;
        bottom: -6px;
        display: block;
        height: 1px;
        width: 100%;
        background-color: $main-text-color;

        transition: transform $effectDuration;
        transform: scale(0);
        transform-origin: left;
      }

      &:hover::after {
        color: $main-text-color;
        transform: scale(1);
      }

      &:hover {
        color: $main-text-color;
      }
    }
  }
}