.page {
  background-color: #fff;
  font-family: "IBM Plex Sans", sans-serif;
  color: $main-text-color;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  scroll-behavior: smooth;

  // font-family: Montserrat, sans-serif;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateY(-100%);
    pointer-events: none;
    height: 100vh;
    background-color: rgba(24, 24, 26, 0.5);

    &:target {
      opacity: 1;
      transform: translateY(0);
      pointer-events: all;
    }
  }
}