.events {
  padding-top: 70px;

  @include onTablet {
    padding-top: 88px;
  }

  @include onDesktop {
    padding-top: 200px;
  }

  &__title {
    margin-bottom: 40px;
    text-align: center;

    @include onTablet {
      text-align: initial;
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 56px;
    }
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 60px;
    margin-bottom: 60px;

    @include onTablet {
      gap: 64px;
      margin-bottom: 112px;
    }

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;

    &:hover .events__card-title::after {
      transition: all $effectDuration;
      animation: jumpUpDown 0.4s ease-in-out 3;
    }

    @include onDesktop {
      @include pageGrid;
    }

    &-img-wrapper {
      display: flex;
      margin-bottom: 16px;
      width: 100%;

      @include onTablet {
        margin-bottom: 44px;
      }

      @include onDesktop {
        grid-column: 1 / 8;
        margin-bottom: 0;
      }
    }

    &-img {
      width: 100%;
      object-fit: cover;
      transition: all $effectDuration;

      @include photo-hover;
    }

    &-content {
      display: flex;
      flex-direction: column;
      gap: 10px;

      @include onTablet {
        gap: 0;
      }

      @include onDesktop {
        grid-column: 9 / -1;
        padding-top: 150px;
        box-sizing: border-box;
      }
    }

    &-exhibition-time {
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 280px;
      font-size: 16px;

      @include onTablet {
        font-size: 14px;
        margin-bottom: 32px;
        max-width: 220px;
      }
    }

    &-exhibition {
      @extend %event-name;
    }

    &-time {
      @extend %green-data;
    }

    &-title {
      position: relative;
      width: max-content;

      @extend %event-card-title;

      &::after {
        content: "";
        position: absolute;
        width: 12px;
        height: 12px;
        background-color: #F14A27;
        top: 35%;
        right: -25px;

        @include onTablet {
          width: 15px;
          height: 15px;
          right: -35px;
        }  
      }

      @include onTablet {
        margin-bottom: 15px;
      }
    }

    &-description {
      color: #4E4E4E;
      max-width: 460px;

      @include onTablet {
        max-width: 66%;
      }

      @include onDesktop {
        max-width: 100%;
      }

      & span {
        display: inline-block;
      }
    }
  }

  &__lecture {
    &-img {
      display: block;
      width: 100%;
      object-fit: contain;
      min-height: 210px;

      @include onTablet {
        min-height: 390px;
      }

      @include onDesktop {
        height: 550px;
      }
    }

    &-content {
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      gap: 20px;
      background-color: #fff;
      margin: 0 auto;
      padding: 20px 0;
      transform: translateY(-56px);
      border-bottom: 2px solid #c6d6d2;
      max-width: 570px;

      @include onTablet {
        transform: translateY(-84px);
        padding: 45px 55px 50px;
        gap: 16px;
      }
      
      @include onDesktop {
        transform: translateY(-102px);
        padding: 45px 100px 50px;
      }
    }

    &-exhibition-time {
      display: flex;
      align-items: center;
      gap: 20px;
      margin: 0 auto;

      @include onTablet {
        gap: 140px;
        margin: 0
      }

      @include onDesktop {
        gap: 130px;
      }
    }

    &-exhibition {
      @extend %event-name;
    }

    &-time {
      @extend %green-data;
    }

    &-title {
      text-align: center;
      @extend %event-card-title;

      @include onTablet {
        text-align: initial;
      }
    }
  }
}