.menu {
  color: #FFF;

  &__background {
    background-color: $color-green;
  }

  &__content {
    padding: 27px 0;
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      padding: 40px 0;
      flex-direction: row;
      gap: 38px;
    }

    @include onDesktop {
      gap: 102px;
    }
  }

  &__nav-section {
    display: flex;
    gap: 28px;

    @include onTablet {
      gap: 38px;
    }
    
    @include onDesktop {
      gap: 102px;
    }
  }

  &__nav {
    max-width: 228px;

    @include onDesktop {
      max-width: 367px;
    }
  }
}