.gallery {
  padding: 44px 0 100px;

  @include onTablet {
    padding: 4px 0 92px;
  }

  @include onDesktop {
    padding: 98px 0 200px;
  }

  &__title {
    text-align: center;
    margin-bottom: 40px;

    @include onTablet {
      text-align: initial;
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 56px;
    }
  }

  &__container {
    display: none;

    @include onDesktop {
      @include pageGrid;
      row-gap: 30px;
    }
  }

  &__img-wrapper {
    transition: all $effectDuration;
    @include photo-hover;

    &--1 {
      grid-column: 1 / 5;
    }

    &--2 {
      grid-column: 5 / -1;
    }

    &--3 {
      grid-column: 1 / 9;
    }

    &--4 {
      grid-column: 9 / -1;
    }
  }

  &__img {
    display: block;
    width: 100%;
  }
}