.line {
  width: 100%;
  height: 1px;
  background-color: #fff;
  opacity: 0.5;

  @include onTablet {
    width: 1px;
    height: 145px;
  }

  &--footer {
    background-color: $color-green;

    @include onTablet {
      height: 1px;
      width: 100%;
    }

    @include onDesktop {
      height: 260px;
      width: 1px;
    }
  }
}