%event-name {
  color: #687480;
  font-weight: 500;
  line-height: 100%;
  font-size: 16px;

  @include onTablet {
    font-size: 14px;
  }
}

%green-data {
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: $color-green;

  @include onTablet {
    font-size: 14px;
  }
}

%event-card-title {
  font-family: Montserrat, sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 100%;

  @include onTablet {
    font-size: 36px;
  }
}