@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline() {
  padding-inline: 20;

  @include onTablet {
    padding-inline: 34;
  }

  @include onDesktop {
    max-width: 1170;
    margin-inline: auto;
    padding-inline: 55px;
  }
}

@mixin photo-hover {
  &:hover {
    transform: scale(1.02);

    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
      -23px 0 20px -23px rgba(0, 0, 0, 0.6),
      23px 0 20px -23px rgba(0, 0, 0, 0.6),
      inset 0 0 40px rgba(0, 0, 0, 0.1);
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effectDuration;

  &:hover {
    #{$property}: $toValue;
  }
}

@mixin pageGrid {
  --colums: 2;
  display: grid;
  column-gap: 20;
  grid-template-columns: repeat(var(--colums), 1fr);

  @include onTablet {
    --colums: 6;
    column-gap: 30;
  }

  @include onDesktop {
    --colums: 12;
  }
}