.subscription {
  padding: 88px 0;
  background-image: url("../images/subscription/subscription-bg-mobile.png");
  background-position: center;
  background-size: cover;

  &__title {
    color: #fff;
    text-align: center;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 16px;
    }
  }

  &__subtitle {
    color: #FFF;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    line-height: 150%;
    margin-bottom: 50px;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDesktop {
      margin-bottom: 72px;
    }
  }

  &__form-wrapper {
    @include onTablet {
      @include pageGrid;
    }
  }

  &__form {
    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 4 / 10;
    }
  }
}