.work-schedule {
  display: flex;
  flex-direction: column;
  gap: 20px;

  &__title {
    font-size: 14px;
    font-weight: 400;
    line-height: 140.625%;

    &--footer {
      margin-bottom: 20px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 16px;
    font-size: 14px;
    font-weight: 400;
    line-height: 150%;

    @include onTablet {
      flex-direction: row;
      gap: 38px;
    }

    @include onDesktop {
      gap: 30px;
    }

    &--footer {
      @include onTablet {
        flex-direction: column;
        gap: 22px;
      }
    }
  }

  &__work {
    @include onTablet {
      width: 158px;
    }

    @include onTablet {
      width: 170px;
    }
  }

  &__weekends {
    @include onTablet {
      max-width: 170px;
    }

    &--footer {
      max-width: 240px;
    }
  }
}