.logo {
  display: flex;
  justify-content: center;
  width: max-content;
  height: 61px;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: 700;
  line-height: 100%;
  text-align: center;
  text-shadow: #fff 0 0 20px;

  transition: transform $effectDuration;

  @include onTablet {
    font-size: 36px;
    height: 72px;
    width: 100%;
  }

  &--contacts {
    text-align: initial;
    justify-content: start;
  }

  &:hover {
    transform: scale(1.05);
  }
}