.header {
  height: min-content;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  overflow: hidden;

  &::after {
    content: "";
    display: block;
    height: 300px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: -1;
    background-image: url("../images/header/header-bg.png");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
  }

  @include onTablet {
    height: 384px;

    &::after {
      background-position: left;
      height: 384px;
      left: 51.5%;
    }
  }

  @include onDesktop {
    height: 654px;

    &::after {
      height: 654px;
      left: 555px;
      width: 725px;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 9px;
    margin-bottom: 250px;

    @include onTablet {
      padding-top: 15px;
      margin-bottom: 40px;
      @include pageGrid;
    }

    @include onDesktop {
      margin-bottom: 119px;
    }
  }

  &__logo {
    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      grid-column: 6 / 8;
    }
  }

  &__bottom {
    @include onTablet {
      @include pageGrid;
    }
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-size: 45px;
    font-weight: 700;
    line-height: 100%;
    color: #000;
    text-align: center;
    margin-bottom: 20px;

    @include onTablet {
      text-align: initial;
      line-height: 120%;
      font-size: 54px;
      margin-bottom: 38px;
      grid-column: span 3;
    }

    @include onDesktop {
      font-size: 72px;
      margin-bottom: 90px;
    }
  }

  &__button {
    @include onTablet {
      grid-column: 1 / 4;
      max-width: 270px;
    }

    @include onDesktop {
      grid-column: 3 / 7;
      width: 100%;
    }
  }

  &__empty-box {
    width: 24px;
  }

  &__event-link {
    display: none;

    @include onDesktop {
      display: inline-block;
      width: max-content;
      color: #687480;
      font-family: IBM Plex Sans;
      font-size: 14px;
      font-style: normal;
      font-weight: 700;
      line-height: 100%;
      position: relative;
      top: -8px;
      transform: rotate(-90deg);
      transition: color 0.3s;

      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        right: 64px;
        width: 70px;
        height: 1px;
        background-color: $color-green;
      }

      &:hover {
        color: $color-green;
      }
    }
  }
}