.form {
  display: flex;
  align-items: center;

  @include onTablet {
    gap: 16px;

    &:hover .button__arrow--orange,
    .form__field:focus+.button__arrow--orange {
      transition: all $effectDuration;
      transform: translateX(-16px);
    }
  }

  @include onDesktop {
    gap: 30px;

    &:hover .button__arrow--orange,
    .form__field:focus+.button__arrow--orange {
      transition: all $effectDuration;
      transform: translateX(-30px);
    }
  }

  &__field {
    box-sizing: border-box;
    width: 100%;
    height: 50px;
    padding-left: 24px;
    border: none;
    outline: none;

    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: $main-text-color;

    @include onTablet {
      height: 70px;
    }
  }
}